.chord {
    margin: 1rem;
}
.chord ul, .chord li {
    list-style-type: none;
    margin: 0;
    padding: 0;
}
.chord li {
    margin-top: 0.5rem;
}
.chord li:first-child {
    margin-top: 0;
}
.note {
    width: 12px;
    height: 12px;
    border-radius: 12px;
}
.note-open {
    background: transparent;
    outline: 2px solid purple;
}
.note-closed {
    background: purple;
    outline: 2px solid purple;
}

.chord::after {
  content: '';
  display: block;
  clear: both;
}

.chord .strings {
    float: left;
    margin-right: 0.5rem;
}
.chord .content ul {
    float: left;
    border-left: 1px solid #aaa;
}
.chord .content ul:first-child {
    border-width: 4px;
}
.chord .content li {
    position: relative;
    height: 20px;
}
.chord .content li .note {
    position: absolute;
    top: 0;
    right: 0;
    margin-right: -6px;
    z-index: 2;
}
.line {
    position: absolute;
    top: 6px;
    left: 0;
    width: 100%;
    height: 1px;
    background: #aaa;
    z-index: 1;
}

.chord.flipped .strings.right,
.chord .strings.left {
    display: block;
}
.chord .strings.right,
.chord.flipped .strings.left {
    display: none;
}

.chord.flipped .content ul:first-child {
    border-width: 0;
}
.chord.flipped .strings ul {
    border-left: 4px solid #aaa;
}
.chord.flipped .strings li {
    margin-left: 0.5rem;
}
